.intro {
    background: url(../img/bg1.png) no-repeat;
    background-size: cover;
    background-position: center center;
    min-height: 624px;
    &_content {
        text-align: center;
    }
    &_h1 {
        text-align: left;
        margin-top: 35px;
        color: #00573b;
        font-size: 80px;
        font-weight: 700;
    }
    &_title {
        display: inline-block;
        margin-top: 100px;
        text-shadow: 5px 9px 29px rgba(0, 0, 0, 0.75);
        color: #fbfbfb;
        font-size: 34px;
        font-weight: 700;
        text-transform: uppercase;
    }
    &_btn {
        margin: 50px auto 0;
        font-size: 31px;
        line-height: 1.5;
        display: block;
        width: 360px;
        padding: 12px 0px;
        border: 4px solid #b72b45;
    }
}