.header {
    &_logo {
        display: inline-block;
        padding-top: 33px;
    }
    &_nav {
        padding-top: 72px;
        &--li {
            display: inline-block;
            margin: 0 22px;
            color: #000;
            &:nth-child(5) {
                color: #fff;
            }
            &:last-child {
                margin-right: 0;
                color: #fff;
            }
        }
        &--a {
            font-size: 15px;
            &:hover {
                text-decoration: underline;
            }
        }
    }
    &_tel {
        padding-top: 51px;
        text-align: center;
        &--number {
            color: #fbfbfb;
            font-size: 20px;
            font-weight: 700;
            text-transform: uppercase;
            &:hover {
                text-decoration: underline;
            }
        }
        &--callback {
            margin-top: 10px;
        }
    }
}