.travel {
    &_title {
        margin-top: 90px;
    }
    &_content {
        position: relative;
        min-height: 505px;
        &::after {
            content: '';
            position: absolute;
            top: -40px; left: 50%;
            transform: translate(-50%, 0%);
            background: url(../img/travel.png) no-repeat;
            width: 584px;
            height: 505px;
            z-index: -1;
        }
    }
    &_first,
    &_two {
        display: flex;
        justify-content: space-between;
        &--item {
            font-size: 28px;
            font-weight: 700;
        }
    }
    &_first {
        min-height: 190px;
        &--item {
            &:nth-child(1) {
                align-self: center;
                max-width: 230px;
                margin-left: 100px;
            }
            &:nth-child(2) {
                align-self: flex-start;
                max-width: 270px;
            }
            &:nth-child(3) {
                align-self: flex-end;
                max-width: 200px;
                margin-right: 80px;
            }
        }
    }
    &_two {
        min-height: 200px;
        align-items: flex-end;
        &--item {
            &:nth-child(1) {
                max-width: 260px;
            }
            &:nth-child(2) {
                max-width: 255px;
                position: relative;
                top: 5px; left: 20px;
            }
        }
    }
    
}