.btn {
    display: inline-block;
    text-align: center;
    padding: 4px 42px 6px;
    background-color: #fff;
    border: 3px solid #b72b45;
    border-radius: 25px;
    color: #b72b45;
    font-size: 15px;
    font-weight: 700;
    transition: .3s all;
    &:hover {
        background-color: #b72b45;
        color: #fff;
    }
}

.h2 {
    text-align: center;
    font-size: 65px;
    font-weight: 700;
    text-transform: uppercase;
}

@media screen and (max-width:1200px) {
    .h2 {
        font-size: 55px;
    }
}
@media screen and (max-width:992px) {
    .h2 {
        font-size: 42px;
    }
}
@media screen and (max-width:768px) {
    .h2 {
        font-size: 25px;
    }
}

.form {
    &_wrap {
        display: flex;
        justify-content: space-between;
    }
    &_input {
        width: 30%;
        padding: 17px 25px 14px;
        border: 4px solid #b72b45;
        border-radius: 25px;
        &::placeholder {
            color: #000;
            font-size: 24px;
            font-weight: 300;
            text-transform: uppercase;
        }
    }
    &_btn {
        display: block;
        text-align: center;
        margin: 35px auto 0;
        padding: 15px 55px;
        background-color: #b72b45;
        border-radius: 25px;
        color: #fff;
        font-size: 23px;
        font-weight: 700;
        text-transform: uppercase;
    }
}