
@media screen and (min-width:1200px) {
    .intro_title {
        br {content: ""}
    }
}
@media screen and (max-width:1200px) {
    .header {
        &_logo {
            padding-top: 28px;
        }
        &_nav {
            padding-top: 55px;
            &--li {
                margin: 0 20px;    
            }
            &--a {
                font-size: 12px;
            }
        }
        &_tel {
            padding-top: 40px;
            &--number {
                font-size: 15px;
            }
            &--callback {
                margin-top: 7px;
                font-size: 12px;
                padding: 4px 33px 5px;
            }
        }
    }
    .intro {
        &_h1 {
            font-size: 65px;
            margin-top: 65px;
        }
        &_title {
         font-size: 28px;
        }
    }
    .trust {
        &_title {
            margin-top: 55px;
        }
        &_item {
            &--img {

            }
            &--text {
                top: -30px;
                font-size: 25px;
            }
        }
    }
}
@media screen and (max-width:992px) {
    .header {
        &_logo {
            padding-top: 15px;
        }
        &_nav {
            padding-top: 30px;
            &--li {
                margin: 0 15px; 
                &:nth-child(5) {
                    color: #000;
                }
            }
            &--a {
                font-size: 9px;
            }
        }
        &_tel {
            padding-top: 25px;
            &--number {
                font-size: 11px;
                text-transform: lowercase;
            }
            &--callback {
                margin-top: 5px;
                font-size: 9px;
                padding: 2px 22px 4px;
            }
        }
    }
    .intro {
        &_h1 {
            font-size: 55px;
            margin-top: 105px;
        }
        &_title {
            font-size: 21px;
            margin-top: 140px;
        }
        &_btn {
            width: 276px;
            font-size: 24px;
            padding: 8px 0;
            margin-top: 35px;
        }
    }
    .trust {
        &_title {
            margin-top: 55px;
        }
        &_item {
            &--text {
                top: -35px;
                font-size: 19px;
            }
        }
    }
  
}
@media screen and (max-width:768px) {
    .header {
        &_logo {
            padding-top: 15px;
        }
        &_nav {
            display: none;
        }
        &_tel {
            padding-top: 10px;
            &--number {
                font-size: 15px;
                color: #00573b;
            }
            &--callback {
                margin-top: 5px;
                font-size: 11px;
                padding: 3px 32px 5px;
            }
        }
    }
    .intro {
        min-height: 500px;
        &_h1 {
            font-size: 35px;
            margin-top: 20px;
            max-width: 210px;
        }
        &_title {
            font-size: 16px;
            margin-top: 30px;
            max-width: 220px;
        }
        &_btn {
            width: 222px;
            font-size: 19px;
            padding: 8px 25px;
            margin-top: 25px;
        }
    }
    .trust {
        &_wrap {
            margin-top: 0px;
        }
        &_item {
            margin: 10px 0;
            &--text {
                top: -25px;
            }
        }
    }

}
@media screen and (min-width:480px) {

}
@media screen and (max-width:360px) {
    &_tel {
        &--number {
            font-size: 10px;
            color: #00573b;
        }
        &--callback {
            margin-top: 5px;
            font-size: 11px;
            padding: 5px 32px;
        }
    }
    .trust {
        &_title {
            padding: 0 10px;
        }
        &_item {
            flex-basis: 50%;
            max-height: 170px;
            &--text {
                font-size: 11px;
                max-width: 130px;
            }
        }
    }

}