.about {
    background: url(../img/about.png) no-repeat;
    background-position: center center;
    background-size: cover;
    margin-top: 120px;
    &_wrap {
        display: flex;
        justify-content: space-between;
    }
    &_left {
        padding: 30px 0 60px;
        max-width: 655px;
        p {
            color: #ffffff;
            font-size: 27px;
            font-weight: 700;
            line-height: 1.4;
        }
    }
    &_right {
        align-self: flex-end;
    }
}