.money {
    &_title {
        margin: 70px auto 25px;
    }
    &_desc {
        text-align: center;
        font-size: 30px;
        font-weight: 700;
        line-height: 35px;
    }
    &_items {
        margin-top: 45px;
        display: flex;
        justify-content: space-between;
    }
    &_li {
        padding-left: 60px;
        margin: 11px 0;
        position: relative;
        &::after {
            content: '';
            position: absolute;
            top: 0; left: 0;
            background: url(../img/li.png) no-repeat;
            width: 37px;
            height: 32px;
        }
        p {
            font-size: 29px;
            font-weight: 700;
        }
    }
    .money_subtitle {
        text-align: center;
        margin: 60px auto 30px;
        color: #b72b45;
        font-size: 54px;
        font-weight: 700;
    }
}