.trust {
    text-align: center;
    &_title {
        margin-top: 55px;
    }
    &_wrap {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
    }
    &_item {
        text-align: center;
        flex-basis: 33.3%;
        max-height: 300px;
        &--img {
            display: block;
            margin: auto;
        }
        &--text {
            display: inline-block;
            position: relative;
            top: -40px;
            color: #010000;
            font-size: 30px;
            font-weight: 700;
        }
    }
}