html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video,
input,
button {
  margin: 0;
  padding: 0;
  border: 0;
}

/* HTML5 display-role reset for older browsers */

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

ol,
ul {
  list-style: none;
}

li {
  list-style: none;
}

:active,
:hover,
:focus {
  outline: 0;
  outline-offset: 0;
}

input {
  border: none;
}

button {
  background: none;
  border: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

:active,
:hover,
:focus {
  outline: 0;
  outline-offset: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

a:hover {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

*,
*:before,
*:after {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

.btn {
  display: inline-block;
  text-align: center;
  padding: 4px 42px 6px;
  background-color: #fff;
  border: 3px solid #b72b45;
  border-radius: 25px;
  color: #b72b45;
  font-size: 15px;
  font-weight: 700;
  transition: .3s all;
}

.btn:hover {
  background-color: #b72b45;
  color: #fff;
}

.h2 {
  text-align: center;
  font-size: 65px;
  font-weight: 700;
  text-transform: uppercase;
}

.form_wrap {
  display: flex;
  justify-content: space-between;
}

.form_input {
  width: 30%;
  padding: 17px 25px 14px;
  border: 4px solid #b72b45;
  border-radius: 25px;
}

.form_input::placeholder {
  color: #000;
  font-size: 24px;
  font-weight: 300;
  text-transform: uppercase;
}

.form_btn {
  display: block;
  text-align: center;
  margin: 35px auto 0;
  padding: 15px 55px;
  background-color: #b72b45;
  border-radius: 25px;
  color: #fff;
  font-size: 23px;
  font-weight: 700;
  text-transform: uppercase;
}

.overlay {
  position: fixed;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.8);
  display: none;
}

.popup {
  position: fixed;
  width: 400px;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  padding: 40px;
  box-sizing: border-box;
  background-color: #fff;
  color: #000;
}

.close-popup {
  position: absolute;
  top: 15px;
  right: 15px;
  width: 23px;
  height: 23px;
  cursor: pointer;
}

.close-popup:before {
  content: '';
  background-color: #000;
  position: absolute;
  height: 1px;
  width: 31px;
  top: 11px;
  left: -4px;
  transform: rotate(-45deg);
}

.close-popup:after {
  content: '';
  background-color: #000;
  position: absolute;
  height: 1px;
  width: 31px;
  top: 11px;
  transform: rotate(45deg);
  left: -4px;
}

.header_logo {
  display: inline-block;
  padding-top: 33px;
}

.header_nav {
  padding-top: 72px;
}

.header_nav--li {
  display: inline-block;
  margin: 0 22px;
  color: #000;
}

.header_nav--li:nth-child(5) {
  color: #fff;
}

.header_nav--li:last-child {
  margin-right: 0;
  color: #fff;
}

.header_nav--a {
  font-size: 15px;
}

.header_nav--a:hover {
  text-decoration: underline;
}

.header_tel {
  padding-top: 51px;
  text-align: center;
}

.header_tel--number {
  color: #fbfbfb;
  font-size: 20px;
  font-weight: 700;
  text-transform: uppercase;
}

.header_tel--number:hover {
  text-decoration: underline;
}

.header_tel--callback {
  margin-top: 10px;
}

.intro {
  background: url(../img/bg1.png) no-repeat;
  background-size: cover;
  background-position: center center;
  min-height: 624px;
}

.intro_content {
  text-align: center;
}

.intro_h1 {
  text-align: left;
  margin-top: 35px;
  color: #00573b;
  font-size: 80px;
  font-weight: 700;
}

.intro_title {
  display: inline-block;
  margin-top: 100px;
  text-shadow: 5px 9px 29px rgba(0, 0, 0, 0.75);
  color: #fbfbfb;
  font-size: 34px;
  font-weight: 700;
  text-transform: uppercase;
}

.intro_btn {
  margin: 50px auto 0;
  font-size: 31px;
  line-height: 1.5;
  display: block;
  width: 360px;
  padding: 12px 0px;
  border: 4px solid #b72b45;
}

.trust {
  text-align: center;
}

.trust_title {
  margin-top: 55px;
}

.trust_wrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.trust_item {
  text-align: center;
  flex-basis: 33.3%;
  max-height: 300px;
}

.trust_item--img {
  display: block;
  margin: auto;
}

.trust_item--text {
  display: inline-block;
  position: relative;
  top: -40px;
  color: #010000;
  font-size: 30px;
  font-weight: 700;
}

.about {
  background: url(../img/about.png) no-repeat;
  background-position: center center;
  background-size: cover;
  margin-top: 120px;
}

.about_wrap {
  display: flex;
  justify-content: space-between;
}

.about_left {
  padding: 30px 0 60px;
  max-width: 655px;
}

.about_left p {
  color: #ffffff;
  font-size: 27px;
  font-weight: 700;
  line-height: 1.4;
}

.about_right {
  align-self: flex-end;
}

.money_title {
  margin: 70px auto 25px;
}

.money_desc {
  text-align: center;
  font-size: 30px;
  font-weight: 700;
  line-height: 35px;
}

.money_items {
  margin-top: 45px;
  display: flex;
  justify-content: space-between;
}

.money_li {
  padding-left: 60px;
  margin: 11px 0;
  position: relative;
}

.money_li::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  background: url(../img/li.png) no-repeat;
  width: 37px;
  height: 32px;
}

.money_li p {
  font-size: 29px;
  font-weight: 700;
}

.money .money_subtitle {
  text-align: center;
  margin: 60px auto 30px;
  color: #b72b45;
  font-size: 54px;
  font-weight: 700;
}

.travel_title {
  margin-top: 90px;
}

.travel_content {
  position: relative;
  min-height: 505px;
}

.travel_content::after {
  content: '';
  position: absolute;
  top: -40px;
  left: 50%;
  transform: translate(-50%, 0%);
  background: url(../img/travel.png) no-repeat;
  width: 584px;
  height: 505px;
  z-index: -1;
}

.travel_first,
.travel_two {
  display: flex;
  justify-content: space-between;
}

.travel_first--item,
.travel_two--item {
  font-size: 28px;
  font-weight: 700;
}

.travel_first {
  min-height: 190px;
}

.travel_first--item:nth-child(1) {
  align-self: center;
  max-width: 230px;
  margin-left: 100px;
}

.travel_first--item:nth-child(2) {
  align-self: flex-start;
  max-width: 270px;
}

.travel_first--item:nth-child(3) {
  align-self: flex-end;
  max-width: 200px;
  margin-right: 80px;
}

.travel_two {
  min-height: 200px;
  align-items: flex-end;
}

.travel_two--item:nth-child(1) {
  max-width: 260px;
}

.travel_two--item:nth-child(2) {
  max-width: 255px;
  position: relative;
  top: 5px;
  left: 20px;
}

.expert_title {
  margin-top: 115px;
}

.expert_desc {
  margin: 5px auto 22px;
  text-align: center;
  font-size: 30px;
  font-weight: 700;
}

.expert_item {
  text-align: center;
}

body {
  font-family: 'Helvetica', 'Arial', sans-serif;
  line-height: 1.2;
  color: #000;
}

.outline {
  border: 1px solid blue;
}

@media screen and (min-width: 1200px) {
  .intro_title br {
    content: "";
  }
}

@media screen and (max-width: 1200px) {
  .h2 {
    font-size: 55px;
  }

  .header_logo {
    padding-top: 28px;
  }

  .header_nav {
    padding-top: 55px;
  }

  .header_nav--li {
    margin: 0 20px;
  }

  .header_nav--a {
    font-size: 12px;
  }

  .header_tel {
    padding-top: 40px;
  }

  .header_tel--number {
    font-size: 15px;
  }

  .header_tel--callback {
    margin-top: 7px;
    font-size: 12px;
    padding: 4px 33px 5px;
  }

  .intro_h1 {
    font-size: 65px;
    margin-top: 65px;
  }

  .intro_title {
    font-size: 28px;
  }

  .trust_title {
    margin-top: 55px;
  }

  .trust_item--text {
    top: -30px;
    font-size: 25px;
  }
}

@media screen and (max-width: 992px) {
  .h2 {
    font-size: 42px;
  }

  .header_logo {
    padding-top: 15px;
  }

  .header_nav {
    padding-top: 30px;
  }

  .header_nav--li {
    margin: 0 15px;
  }

  .header_nav--li:nth-child(5) {
    color: #000;
  }

  .header_nav--a {
    font-size: 9px;
  }

  .header_tel {
    padding-top: 25px;
  }

  .header_tel--number {
    font-size: 11px;
    text-transform: lowercase;
  }

  .header_tel--callback {
    margin-top: 5px;
    font-size: 9px;
    padding: 2px 22px 4px;
  }

  .intro_h1 {
    font-size: 55px;
    margin-top: 105px;
  }

  .intro_title {
    font-size: 21px;
    margin-top: 140px;
  }

  .intro_btn {
    width: 276px;
    font-size: 24px;
    padding: 8px 0;
    margin-top: 35px;
  }

  .trust_title {
    margin-top: 55px;
  }

  .trust_item--text {
    top: -35px;
    font-size: 19px;
  }
}

@media screen and (max-width: 768px) {
  .h2 {
    font-size: 25px;
  }

  .popup {
    width: 100%;
    height: 100vh;
    font-size: 15px;
  }

  .header_logo {
    padding-top: 15px;
  }

  .header_nav {
    display: none;
  }

  .header_tel {
    padding-top: 10px;
  }

  .header_tel--number {
    font-size: 15px;
    color: #00573b;
  }

  .header_tel--callback {
    margin-top: 5px;
    font-size: 11px;
    padding: 3px 32px 5px;
  }

  .intro {
    min-height: 500px;
  }

  .intro_h1 {
    font-size: 35px;
    margin-top: 20px;
    max-width: 210px;
  }

  .intro_title {
    font-size: 16px;
    margin-top: 30px;
    max-width: 220px;
  }

  .intro_btn {
    width: 222px;
    font-size: 19px;
    padding: 8px 25px;
    margin-top: 25px;
  }

  .trust_wrap {
    margin-top: 0px;
  }

  .trust_item {
    margin: 10px 0;
  }

  .trust_item--text {
    top: -25px;
  }
}

@media screen and (max-width: 360px) {
  _tel--number {
    font-size: 10px;
    color: #00573b;
  }

  _tel--callback {
    margin-top: 5px;
    font-size: 11px;
    padding: 5px 32px;
  }

  .trust_title {
    padding: 0 10px;
  }

  .trust_item {
    flex-basis: 50%;
    max-height: 170px;
  }

  .trust_item--text {
    font-size: 11px;
    max-width: 130px;
  }
}